import View from './view.js'

class ResultsView extends View {
    _parentElement = document.querySelector('.results')
    _errorMessage = `No recipes found for your query. Please try again!`
    _message = ''

    _generateMarkup() {
        // console.log(this._data)
        return this._data.map(this._generateMarkupPreview).join("")
        
    }
    _generateMarkupPreview(result) {
        // console.log(result)
        return `
        <div class="preview duration-300 hover:bg-[#f9f5f3]">
            <a href="${result.id}" class="preview__link flex items-center py-[3rem] px-[2rem] duration-300 border border-r-2 border-white border-solid">
                <figure class="preview__fig grow-0 shrink-0 basis-[5.8rem] h-[5.8rem] rounded-[50%] border-none mr-[2rem] relative overflow-hidden">
                    <img src="${result.image}" class="block w-full h-full object-cover">
                </figure>
                <div class="preview__data grid gap-y-[.1rem] items-center">
                    <h4 class="preview__title col-span-full uppercase font-semibold text-[1.5rem] text-result-text text-ellipsis overflow-hidden whitespace-nowrap">${result.title}</h4>
                    <p class="preview__publisher uppercase font-semibold text-[1.2rem] text-[#918581]">${result.publisher}</p>
                    <div class="preview__user-generated bg-[#eeeae8] flex items-center justify-center h-[2.4rem] w-[2.4rem] rounded-[10rem] ml-auto mr-[1.5rem]">
                        <i class="fa-regular fa-user text-[1.5rem] text-result-text"></i>
                    </div>
                </div>
            </a>
        </div> 
        `
    }
    
}

export default new ResultsView() 